import i18n from 'i18n';
import acceptLanguage from 'accept-language';

acceptLanguage.languages(['en-US', 'fr-FR', 'de-DE', 'ja-JP']);
i18n.configure({
  locales: ['en', 'fr', 'de', 'ja'],
  directory: `${__dirname}/../config/locales`,
  defaultLocale: 'en',
});

export default i18n;
export {
  acceptLanguage,
};
